import React, { useState, useRef , useEffect} from 'react';
//import React, { useEffect, useState } from 'react';
import './App.css';
import { ThemeProvider } from 'styled-components';
import { useOnClickOutside } from './hooks';
import { GlobalStyles } from './global';
import { theme } from './theme';
import { Burger, Menu } from './components';
import FocusLock from 'react-focus-lock';
import DatabaseDemo from './components/DatabaseDemo/DatabaseDemo'
import Home from './components/Home/Home'
import {
  HashRouter as Router,
  Switch,
  Route,
} from "react-router-dom";


function App() {
  const [open, setOpen] = useState(false);
  const [transactions, setTransactions] = useState([]);
  const node = useRef();
  const menuId = "main-menu";

  useOnClickOutside(node, () => setOpen(false));
  //// 2. Your getTransactions function
  async function getTransactions() {
    try {
      const res = await fetch('/api/transaction');
      const data = await res.json();
      console.log('All transactions:', data.result);
      setTransactions(data.result || []); // Store the data in state
    } catch (err) {
      console.error('Error fetching transactions:', err);
    }
  }

  // 3. Your addTransaction function
  async function addTransaction(amount, desc) {
    try {
      const res = await fetch('/api/transaction', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({ amount, desc })
      });
      const data = await res.json();
      console.log('Server response:', data);
      
      // Refresh the list after adding
      getTransactions(); 
    } catch (err) {
      console.error('Error adding transaction:', err);
    }
  }

  // 4. Automatically fetch data when the component loads
  useEffect(() => {
    getTransactions();
  }, []);

  return (
        <ThemeProvider theme={theme}>
            <GlobalStyles />
            <div ref={node}>
              <FocusLock disabled={!open}>
                <Burger open={open} setOpen={setOpen} aria-controls={menuId} />
                <Router>
                  <div>
                    <Menu open={open} setOpen={setOpen} id={menuId} />
                    <Switch>
                      <Route path="/db">
                        {/* 2. PASS DATA TO THE COMPONENT HERE */}
                        <DatabaseDemo 
                           transactions={transactions} 
                           addTransaction={addTransaction} 
                        />
                      </Route>
                      <Route path="/">
                        <Home/>
                      </Route>
                    </Switch>
                  </div>
                </Router>
              </FocusLock>
            </div>
        </ThemeProvider>
  );
}

export default App;
